import Vue from 'vue';

export default Vue.component(
    'guardian',
    {
        props: {
            global: Object,
        },

        data: function() {
            return {
                translation: {
                    'priest': 'Priester',
                    'baron': 'Baron',
                    'maid': 'Zofe',
                    'prince': 'Prinz',
                    'king': 'Knig',
                    'countess': 'Grfin',
                    'princess': 'Prinzessin'
                }
            }
        },
        template: `
            <div class="window">
                <div class="window__header">Whle die Karte, die {{global.guardianEffectChosenPlayer}} auf der Hand hlt!</div>
                <div class="window__body">
                    <div class="window__players">
                        <div
                            class="window__player button"
                            v-for="card in global.guardianEffectSelectableCards"
                            @click="$emit('send', {action: 'chooseGuardianEffectCard', params: {card: card}})"
                        >
                        {{translation[card]}}
                        </div>
                    </div>
                </div>
            </div>
    `
    }
);
